.secret-wirte-form {
  box-sizing: border-box;
  padding: 0 0.625rem 5rem; }
  .secret-wirte-form .audio-wrap {
    display: flex;
    justify-content: space-between; }
  .secret-wirte-form .label {
    font-size: 1.125rem;
    color: #000; }
  .secret-wirte-form .am-list-header {
    padding: 0.938rem 0 0.313rem 0; }
  .secret-wirte-form .no-bg .am-list-body {
    background: none; }
    .secret-wirte-form .no-bg .am-list-body::before, .secret-wirte-form .no-bg .am-list-body::after {
      content: '';
      display: none !important; }
  .secret-wirte-form .no-bg .am-image-picker-list {
    padding-left: 0; }
  .secret-wirte-form .code-wrap {
    display: flex;
    align-items: center; }

.secret-check-wrap .result {
  margin-top: 13.125rem;
  padding: 0 2.500rem 5rem; }
  .secret-check-wrap .result .item {
    display: flex;
    margin-bottom: 1.250rem; }
  .secret-check-wrap .result .label {
    font-size: 0.875rem;
    width: 9.375rem; }
  .secret-check-wrap .result .content {
    width: 12.500rem; }
    .secret-check-wrap .result .content.light {
