      font-size: 1rem; }
    .secret-check-wrap .result .content img {
      width: 5rem;
      height: 5rem; }
  .secret-check-wrap .result .url-btn {
    position: absolute;
    bottom: 1.875rem;
    left: 50%;
    transform: translateX(-50%);
    color: #108ee9; }

.secret-check-form-wrap {
  padding: 0 0.938rem;
  margin-top: 15.625rem; }
  .secret-check-form-wrap .tip p {
    line-height: 1.250rem;
    margin-bottom: 0.625rem; }

.secret-check-form {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-top: 1.250rem; }
  .secret-check-form .am-list-item {
    height: 2.500rem !important;
    min-height: auto; }
  .secret-check-form .search-btn {
    margin-left: 0.625rem;
    height: 2rem;
    line-height: 2rem;
    display: block;
    font-size: 0.750rem;
    padding: 0;
    width: 3.750rem; }
